<template>
  <div class="feedback-page">
    <van-nav-bar
      fixed
      border
      title="意见反馈"
      left-arrow
      @click-left="goBack"
    />

    <van-cell-group :border="false">
      <van-cell
        title="您提交的问题，我们会尽快处理并与您联系"
        :border="false"
      />
      <van-field
        class="textarea"
        v-model="form.content"
        type="textarea"
        :border="false"
        :autosize="{ maxHeight: 180, minHeight: 120 }"
        :error-message="errors.content"
        @input="errors.content = ''"
        placeholder="请描述您遇到的问题..."
      />
    </van-cell-group>
	<img width="150" height="150" style="text-align: center;" class="dialog-img" src="../../public/img/img5.jpg">
	<p style="text-align: center;">长按或扫一扫添加微信</p>
	<p style="text-align: center;">如需帮助，请拨打平台管理员电话<br/>15602908103</p>
    <van-button @click="handleSubmit" class="bottom-bar" block type="danger"
      >提交意见</van-button
    >
  </div>
</template>
<script>
import { mapGetters, mapState } from "vuex";
import { Button, NavBar, CellGroup, Cell, Field,Image  } from "vant";
import { feedback } from "../api/base";

export default {
  name: "feedback",
  components: {
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
	[Image.name]: Image,
	 
  },
  data() {
    return {
      form: { content: "" },
      errors: { content: "" }
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"])
  },
  methods: {
    // 点击返回按钮
    goBack() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },
    async handleSubmit() {
      if (!this.form.content) {
        this.errors.content = "请描述您遇到的问题";
        return;
      }
      await feedback(this.form.content, this.userInfo.id);
      this.$dialog
        .alert({
          title: "温馨提示",
          message: "感谢您的反馈，我们将尽快处理并与您联系",
          confirmButtonText: "确认"
        })
        .then(() => {
          this.goBack();
        });
    }
  }
};
</script>

<style lang="less" scoped>
.dialog-img{
	width: 150px;
	height: 150px;
	display: block;
	margin: 0 auto;
}	
.feedback-page {
  background-color: #fff;
}

.cell-group {
  margin-top: 12px;
}

.feedback-page {
  padding-bottom: 45px;
}

.bottom-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }

  .feedback-page {
    padding-bottom: 45px;
  }
}
@media (min-width:750px) {
	.dialog-img{
		width: 150px;
		height: 150px;
		display: block;
		margin: 0 auto;
	}	
}
</style>
